/**
 * Validation utilities for forms
 */

export interface ValidationResult {
  isValid: boolean;
  error?: string;
}

// Password validation rules
export const PASSWORD_RULES = {
  minLength: 4,
  requireUppercase: true,
  requireSymbol: true,
};

/**
 * Validate password
 * - Minimum 4 characters
 * - At least one uppercase letter
 * - At least one symbol (!@#$%^&*()_+-=[]{}|;':",.<>?/)
 */
export function validatePassword(password: string): ValidationResult {
  if (!password) {
    return { isValid: false, error: ' ' };
  }

  if (password.length < PASSWORD_RULES.minLength) {
    return { isValid: false, error: `    ${PASSWORD_RULES.minLength} ` };
  }

  if (PASSWORD_RULES.requireUppercase && !/[A-Z-]/.test(password)) {
    return { isValid: false, error: '       ' };
  }

  if (PASSWORD_RULES.requireSymbol && !/[!@#$%^&*()_+\-=\[\]{}|;':",.<>?\/\\`~]/.test(password)) {
    return { isValid: false, error: '        (!@#$%^&*  ..)' };
  }

  return { isValid: true };
}

/**
 * Validate name part (first name, last name, middle name)
 * - Minimum 2 characters
 * - No spaces
 * - Only letters (cyrillic and latin)
 * - Capitalizes first letter
 */
export function validateNamePart(name: string, fieldName: string = ''): ValidationResult {
  if (!name) {
    return { isValid: false, error: `${fieldName} ` };
  }

  const trimmed = name.trim();

  if (trimmed.length < 2) {
    return { isValid: false, error: `${fieldName}    2 ` };
  }

  if (/\s/.test(trimmed)) {
    return { isValid: false, error: `${fieldName}    ` };
  }

  if (!/^[a-zA-Z--]+$/.test(trimmed)) {
    return { isValid: false, error: `${fieldName}    ` };
  }

  return { isValid: true };
}

/**
 * Capitalize first letter of a name
 */
export function capitalizeName(name: string): string {
  if (!name) return '';
  const trimmed = name.trim().toLowerCase();
  return trimmed.charAt(0).toUpperCase() + trimmed.slice(1);
}

/**
 * Validate email format
 */
export function validateEmail(email: string): ValidationResult {
  if (!email) {
    return { isValid: false, error: 'Email ' };
  }

  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  if (!emailRegex.test(email)) {
    return { isValid: false, error: '  email' };
  }

  return { isValid: true };
}

/**
 * Validate phone number (Belarus format)
 */
export function validatePhone(phone: string): ValidationResult {
  if (!phone) {
    return { isValid: false, error: ' ' };
  }

  // Remove all non-digit characters
  const digits = phone.replace(/\D/g, '');

  // Belarus phone: 375 + 9 digits = 12 digits total
  if (digits.length !== 12) {
    return { isValid: false, error: '     +375 (XX) XXX-XX-XX' };
  }

  if (!digits.startsWith('375')) {
    return { isValid: false, error: '    +375' };
  }

  return { isValid: true };
}

/**
 * Validate login
 * - Minimum 3 characters
 * - Only letters, numbers, underscores
 * - No spaces
 */
export function validateLogin(login: string): ValidationResult {
  if (!login) {
    return { isValid: false, error: ' ' };
  }

  const trimmed = login.trim();

  if (trimmed.length < 3) {
    return { isValid: false, error: '    3 ' };
  }

  if (/\s/.test(trimmed)) {
    return { isValid: false, error: '    ' };
  }

  if (!/^[a-zA-Z0-9_]+$/.test(trimmed)) {
    return { isValid: false, error: '     ,   _' };
  }

  return { isValid: true };
}

/**
 * Validate text field (subject, message)
 */
export function validateTextField(text: string, fieldName: string, minLength: number = 3, maxLength: number = 1000): ValidationResult {
  if (!text) {
    return { isValid: false, error: `${fieldName} ` };
  }

  const trimmed = text.trim();

  if (trimmed.length < minLength) {
    return { isValid: false, error: `${fieldName}    ${minLength} ` };
  }

  if (trimmed.length > maxLength) {
    return { isValid: false, error: `${fieldName}    ${maxLength} ` };
  }

  return { isValid: true };
}

/**
 * Validate review/feedback
 */
export function validateFeedback(data: { name: string; text: string; grade: number }): ValidationResult {
  // Validate name
  const nameResult = validateTextField(data.name, '', 2, 100);
  if (!nameResult.isValid) return nameResult;

  // Check name doesn't have numbers
  if (/\d/.test(data.name)) {
    return { isValid: false, error: '    ' };
  }

  // Validate text
  const textResult = validateTextField(data.text, ' ', 10, 2000);
  if (!textResult.isValid) return textResult;

  // Validate grade
  if (data.grade < 1 || data.grade > 5) {
    return { isValid: false, error: '    1  5' };
  }

  return { isValid: true };
}

/**
 * Validate letter to chief doctor
 */
export function validateLetter(data: { subject: string; content: string }): ValidationResult {
  // Validate subject
  const subjectResult = validateTextField(data.subject, ' ', 3, 200);
  if (!subjectResult.isValid) return subjectResult;

  // Validate content
  const contentResult = validateTextField(data.content, '', 10, 5000);
  if (!contentResult.isValid) return contentResult;

  return { isValid: true };
}
